// services/TimeControl.js
import axios from './configApi';

const TimeControlService = {
    registerEntry: async (entryData) => {
        try {
            const response = await axios.post('/time-control/entry', entryData);
            return {
                ok: true,
                data: response.data
            };
        } catch (error) {
            return { ok: false, error: error.response?.data?.error || error.message };
        }
    },
}

export default TimeControlService;
