import React, { useEffect, useState } from 'react';
import { makeStyles } from '@mui/styles';
import { Typography, Box, FormControl, Select, MenuItem, Button, Switch, FormControlLabel, FormGroup, Snackbar, Alert } from '@mui/material';
import { useLocation, useNavigate, useParams } from 'react-router-dom';
import ClassService from '../services/Class';
import SubjectService from '../services/Subject';
import AuthService from '../services/Auth';
import Navbar from '../Components/NavBar';
import { classDetailsPageStyles } from '../styles/styles';
import TimeControlService from '../services/TimeControl';

const ClassDetailsPage = () => {
    const navigate = useNavigate();
    let { id } = useParams();
    const classes = classDetailsPageStyles();
    const location = useLocation();
    const [selectedSubject, setSelectedSubject] = useState('');
    const [classDetail, setClassDetail] = useState({
        class_name: '',
        academic_year: ''
    });
    const [subjects, setSubjects] = useState([]);
    const [isRegistered, setIsRegistered] = useState(false);
    const [snackbarOpen, setSnackbarOpen] = useState(false);
    const [snackbarMessage, setSnackbarMessage] = useState('');
    const [snackbarSeverity, setSnackbarSeverity] = useState('error');

    const handleSubjectChange = (event) => {
        setSelectedSubject(event.target.value);
    };

    useEffect(() => {
        const findClass = async () => {
            try {
                const response = await ClassService.find(id);
                setClassDetail(response.data);
            } catch (error) {
                console.error('Erro ao buscar classes:', error);
            }
        };
        findClass();
    }, [id]);

    useEffect(() => {
        const getSubjects = async () => {
            try {
                const response = await SubjectService.getAll();
                setSubjects(response.data);
            } catch (error) {
                console.error('Erro ao buscar as materias:', error);
            }
        };
        getSubjects();
    }, []);

    const handleToggleChange = async (event) => {
        if (!selectedSubject) {
            setSnackbarMessage('Por favor, selecione uma matéria.');
            setSnackbarSeverity('error');
            setSnackbarOpen(true);
            setIsRegistered(false);
            return;
        }

        setIsRegistered(event.target.checked);

        if (event.target.checked) {
            // try {
                const currentUser = AuthService.getCurrentUser();
                console.log('currentUser', currentUser)
                const entryData = {
                    teacher_id: currentUser.id,
                    class_id: id,
                    entry_datetime: new Date().toISOString(),
                };

                const response = await TimeControlService.registerEntry(entryData);

                if (response.ok) {
                    setSnackbarMessage('Entrada registrada com sucesso!');
                    setSnackbarSeverity('success');
                    setTimeout(() => { navigate('/'); }, 1000)

                } else {
                    setSnackbarMessage(response.error);
                    setSnackbarSeverity('error');
                    setIsRegistered(false)
                }

            // } catch (error) {
            //     setSnackbarMessage('Não foi possivel registrar a entrada');
            //     setSnackbarSeverity('error');
            // }
            setSnackbarOpen(true);
        }
    };

    if (snackbarOpen) {
        return (
            <>
                <Navbar />

                <Box className={classes.container}>
                    <Snackbar open={snackbarOpen} autoHideDuration={6000} onClose={() => setSnackbarOpen(false)} anchorOrigin={{ vertical: 'top', horizontal: 'center' }}>
                        <Alert onClose={() => setSnackbarOpen(false)} severity={snackbarSeverity} sx={{ width: '100%' }}>
                            {snackbarMessage}
                        </Alert>
                    </Snackbar>
                </Box>
            </>
        )
    }

    return (
        <>

            <Navbar />

            <Box className={classes.container}>
                <div>
                    <Typography variant="h4" className={classes.header}>Turma do {classDetail.class_name.toUpperCase()}</Typography>
                    <Typography variant="h6" className={classes.typographyStyles}>Ano: {classDetail.academic_year}</Typography>
                    <Typography variant="h6" className={classes.typographyStyles}>Professor: Gleisin</Typography>
                </div>


                <FormControl variant="outlined" className={classes.formControl}>
                    <Select
                        value={selectedSubject}
                        onChange={handleSubjectChange}
                        displayEmpty
                        className={classes.selectEmpty}
                        inputProps={{ 'aria-label': 'Without label' }}
                    >
                        <MenuItem value="" disabled>
                            Selecione uma Matéria
                        </MenuItem>
                        {subjects.map((subject) => (
                            <MenuItem key={subject.id} value={subject.id}>{subject.name}</MenuItem>
                        ))}
                    </Select>
                </FormControl>
                <div className={classes.switchContainer}>
                    <FormControlLabel
                        control={
                            <Switch
                                checked={isRegistered}
                                onChange={handleToggleChange}
                                classes={{ switchBase: classes.switchBase, checked: classes.checked }}
                                label={isRegistered ? 'Entrada Registrada' : 'Registrar Entrada na Turma'}
                            />
                        }
                        label="Registrar Entrada na Turma"
                    />
                </div>
            </Box>
        </>
    );
};

export default ClassDetailsPage;
