import React from 'react';
import { Card, CardContent, Typography, CardActionArea } from '@mui/material';
import { makeStyles } from '@mui/styles';
import { themes } from '../Helpers/Theme';
import { useNavigate } from 'react-router-dom';

const CardComponent = ({ title, body, id }) => {
  const navigate = useNavigate();

  const CardStyles = makeStyles((theme) => ({
    cardwrapper: {
      '&:hover': {
        backgroundColor: themes.palette.primary.dark, // Cor de fundo no hover
        cursor: 'pointer', // Muda o cursor para indicar que é clicável
        transform: 'scale(1.05)', // Efeito de zoom leve
        transition: 'transform 0.3s ease-in-out', // Transição suave
      },
      backgroundColor: themes.palette.primary.bgColor,
      borderRadius: '8px',
      minHeight: 200,
      margin: 10,
      boxShadow: "2px 2px 2px 1px #00000f", // Sombra para dar profundidade
    },
    cardtitle: {
      color: '#00000f',
    },
    cardtext: {
      textAlign: 'center',
    },
  }));

  const classes = CardStyles();

  const handleClick = () => {
    // Navega para uma rota específica ou executa uma ação ao clicar no card
    navigate(`/class/${id}`); // Exemplo de navegação
  };

  return (
    <Card className={classes.cardwrapper} onClick={handleClick}>
      <CardActionArea>
        <CardContent>
          <Typography variant="h5" component="div" className={classes.cardtitle}>
            {title}
          </Typography>
          <Typography color="text.secondary" className={classes.cardtext}>
            Ano Acadêmico: {body}
          </Typography>
        </CardContent>
      </CardActionArea>
    </Card>
  );
};


export default CardComponent;
